.feedback-paginator {
  display: flex;
}

.feedback-paginator__button {
  display: flex;
  justify-content: center;
  width: 130px;
  min-height: 52px;
  border: 1px solid @border-gray;
  background-color: @white;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .feedback-paginator {
    position: absolute;
    bottom: 31px;
    right: 3px;
    width: 175px;
  }

  .feedback-paginator__button {
    border: none;
  }
}

@media (min-width: @desktop-width) {
  .feedback-paginator {
    bottom: 33px;
    right: -56px;
  }
}
