.main-nav {
  width: 303px;
  margin: 0 auto;
}

.main-nav__link {
  color: @black;
  position: relative;
  min-height: 25px;
  padding-left: 67px;
  display: block;

  &:hover {
    color: rgba(0, 0, 0, 0.6);
  }

  &:active {
    color: rgba(0, 0, 0, 0.3);
  }

  &--search:hover .main-nav__search-icon,
  &--basket:hover .main-nav__basket-icon {
    fill: @lightgreen;
    opacity: 1;
  }

  &--search:active .main-nav__search-icon,
  &--basket:active .main-nav__basket-icon {
    opacity: 0.3;
  }
}

.main-nav__search-icon {
  position: absolute;
  top: 3px;
  left: 25px;
  opacity: 0.2;
}

.main-nav__basket-icon {
  position: absolute;
  top: 0;
  left: 23px;
  opacity: 0.2;
}

.main-nav__list {
  .list-reset();

  &--newstaff-and-sale {
    display: none;
  }
}

.main-nav__list-item {
  padding-top: 27px;
  padding-bottom: 27px;
  border-top: 1px solid @border-gray;
}

.main-nav__free-delivery {
  display: none;
}

.main-nav-hidden {
  display: none;
}

@media (min-width: @tablet-width) {
  .main-nav {
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 751px;
    margin: 0 auto;
  }

  .main-nav__list {
    .list-reset();
    display: flex;
    align-items: center;

    &--catalog-and-order {
      font-size: 15px;
      order: 3;
      padding-left: 8px;
      padding-top: 17px;
    }

    &--catalog-and-order-ru {
      font-size: 15px;
      order: 3;
      padding-left: 39px;
      padding-top: 17px;
    }

    &--search-and-basket {
      padding-right: 42px;
      order: 2;
      background-color: @white;
    }

    &--newstaff-and-sale {
      display: none;
    }
  }

  .main-nav__list-item {
    padding: 0;
    border: none;

    &--catalog {
      width: 135px;
    }

    &--order {
      width: 208px;
    }

    &--search {
      width: 80px;
      padding-top: 20px;
      padding-bottom: 21px;
      border-right: 1px solid @border-gray;
      border-left: 1px solid @border-gray;
    }

    &--basket {
      width: 265px;
      text-align: right;
    }

    &--search-and-basket:last-child {
      border: none;
    }
  }

  .main-nav__free-delivery {
    font-size: 15px;
    font-weight: 400;
    display: block;
    margin: 0;
    padding-top: 17px;
    padding-right: 42px;
    order: 3;
  }

  .main-nav__link {
    padding-left: 0;

    &--search {
      visibility: hidden;
    }

    &--basket {
      padding-left: 55px;
    }
  }

  .main-nav__basket-icon {
    top: 2px;
    left: 33px;
  }

  .main-nav__search-icon {
    visibility: visible;
    top: 17px;
    left: 33px;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    width: 1133px;
  }

  .main-nav__list {
    &--catalog-and-order {
      font-size: inherit;
      order: 1;
      padding-left: 43px;
      padding-top: 0;
      background-color: @white;
    }

    &--newstaff-and-sale {
      font-size: 15px;
      display: flex;
      padding-top: 18px;
      padding-left: 0;
      margin-left: -5px;
      order: 5;
    }

    &--newstaff-and-sale-ru {
      font-size: 15px;
      display: flex;
      padding-top: 18px;
      padding-left: 25px;
      order: 5;
    }

    &--search-and-basket {
      order: 4;
      padding-right: 43px;
    }
  }

  .main-nav__list-item {
    &--catalog {
      width: 150px;
      text-align: left;
      border-right: 1px solid @border-gray;
      padding-top: 32px;
      padding-bottom: 32px;
      padding-right: 35px;
    }

    &--order {
      width: 185px;
      text-align: right;
      padding-top: 32px;
      padding-bottom: 32px;
    }

    &--newstaff {
      width: 190px;
    }

    &--sale {
      width: 140px;
      text-align: right;
    }
  }

  .main-nav__free-delivery {
    padding-top: 18px;
    padding-right: 41px;
    order: 6;
  }
}
